@use "../../../../assets/styles/variables" as *;
@use "../../../../assets/styles/mixins" as *;

.specialties-section {
  padding: $padding-container-large 0;
  background-color: $white-color;
  overflow: hidden;
  position: relative;
  
  &__container {
    @include section-container;
    max-width: 1700px;
  }
  
  &__header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: $spacing-xl;
    
    @include media($breakpoints-mobile) {
      flex-direction: column;
      align-items: flex-start;
      gap: $spacing-md;
    }
  }
  
  &__title {
    @include font($font-size-h2, $font-weight-bold, $main-color);
    
    @include media($breakpoints-tablet) {
      font-size: $font-size-h3;
    }
  }
  
  &__view-all {
    color: $accent-color;
    font-weight: $font-weight-medium;
    text-decoration: none;
    transition: $transition;
    font-size: $font-size-text-medium;
    
    @include hover{
      color: darken($accent-color, 10%);
      text-decoration: underline;
    }
  }
  
  &__progress {
    height: 4px;
    background-color: rgba($main-color, 0.1);
    border-radius: 2px;
    margin-bottom: $spacing-md;
    width: 100%;
    overflow: hidden;
    
    @include media($breakpoints-mobile) {
      display: none;
    }
  }
  
  &__progress-bar {
    height: 100%;
    width: 0;
    background-color: $accent-color;
    border-radius: 2px;
  }
  
  &__horizontal {
    display: flex;
    gap: $spacing-xl;
    width: max-content;
    will-change: transform;
    
    @include media($breakpoints-mobile) {
      display: none;
    }
  }
  
  &__card-wrapper {
    width: 500px;
    flex-shrink: 0;
    transition: transform 0.3s ease;
    
    @include media($breakpoints-tablet-landscape) {
      width: 300px;
    }
  }
  
  &__grid {
    display: none;
    grid-template-columns: repeat(3, 1fr);
    gap: $spacing-xl;
    
    @include media($breakpoints-tablet-landscape) {
      grid-template-columns: repeat(2, 1fr);
      gap: $spacing-lg;
    }
    
    @include media(670px) {
      grid-template-columns: 1fr;
    }
    
    @include media($breakpoints-mobile) {
      display: grid;
    }
  }
  
  &__card {
    height: 100%;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
    
    @include hover{
      box-shadow: $shadow-lg;
      transform: translateY(-5px);
    }
  }
}

@media (max-width: 1024px) {
  .specialties-section {
    padding: $spacing-lg 0;
    min-height: auto;
    
    &__horizontal {
      display: none;
    }
    
    &__card {
      width: 100%;
      max-width: 550px;
      margin: 0 auto;
    }
    
    &__progress {
      display: none;
    }
    
    &__grid {
      display: grid;
      grid-template-columns: repeat(2, 1fr);
      gap: $spacing-md;
    }
  }
}

@include media(550px) {
  .specialties-section {
    &__grid {
      @include grid(1, auto, $spacing-sm);
    }
  }
}
